import React from "react";

const CsvUpload = ({ onFileUpload }) => {
  const handleFileChange = (event) => {
    const file = event.target.files[0];
    if (file) {
      onFileUpload(file);
    }
  };

  return (
    <div className="flex flex-row rounded-md border border-gray-300 bg-blue-100 p-4">

        <label
          className="block mb-2 text-sm font-medium text-blue-900 mt-1"
          style={{ width: "200px", fontWeight: "bold" }}
        >
          Upload CSV
        </label>
        <input
          type="file"
          accept=".csv"
          onChange={handleFileChange}
          className="block w-full text-sm text-gray-900 border border-blue-300 rounded-lg cursor-pointer bg-blue-500 text-white py-2 px-4"
        />
      </div>
  );
};

export default CsvUpload;
