import React, { useRef, useEffect, useState } from "react";
import * as d3 from "d3";
import html2canvas from "html2canvas";

const BarChart = ({ data, maxRecipesRunPerDay }) => {
  const ref = useRef();
  const wrapperRef = useRef();
  const [colorScheme, setColorScheme] = useState("schemeCategory10");
  const [yTicks, setYTicks] = useState([]);
  const [numYTicks, setNumYTicks] = useState(5); // Default number of y-ticks

  const [width, setWidth] = useState(900);
  const [height, setHeight] = useState(1300);
  const handleWidthChange = (e) => {
    setWidth(parseInt(e.target.value));
  };

  const handleHeightChange = (e) => {
    setHeight(parseInt(e.target.value));
  };

  const handleNumYTicksChange = (e) => {
    const numYTicks = parseInt(e.target.value);
    const yScale = d3
      .scaleLinear()
      .domain([0, d3.max(data, (d) => d.value)])
      .nice()
      .range([height - 60, 40]);

    const yTicks = yScale.ticks(numYTicks).filter((d) => d !== 0);
    setYTicks(yTicks);
  };

  const downloadChart = () => {
    html2canvas(wrapperRef.current)
      .then((canvas) => {
        const link = document.createElement("a");
        link.href = canvas.toDataURL("image/png");
        link.download = "chart.png";
        link.click();
      })
      .catch((err) => console.error("Error capturing chart:", err));
  };

  useEffect(() => {
    const svg = d3.select(ref.current);
    svg.selectAll("*").remove();

    const margin = { top: 20, right: 30, bottom: 40, left: 40 };
    const chartWidth = width - margin.left - margin.right;
    const chartHeight = height - margin.top - margin.bottom;

    // Filter out recipes with run_duration < 2 min
    const filteredData = data.filter((d) => d.run_duration >= 2);

    // Calculate derived fields
    filteredData.forEach((d) => {
      d.num_runs_per_day =
        (24 * 60) / parseInt(d.frequency_of_run.split(" ")[1]);
      d.total_run_time_per_day = parseFloat(
        (d.num_runs_per_day * d.run_duration).toFixed(1)
      );
    });

    const stackData = d3
      .stack()
      .keys(filteredData.map((d) => d.recipe_name))
      .value((d, key) => {
        const recipe = d.find((r) => r.recipe_name === key);
        return recipe ? recipe.num_runs_per_day : 0;
      })([filteredData]);

    const xScale = d3
      .scaleBand()
      .domain([0]) // Single stacked bar
      .range([0, chartWidth])
      .padding(0.1);

    const yScale = d3
      .scaleLinear()
      .domain([0, d3.max(stackData, (d) => d3.max(d, (d) => d[1]))])
      .nice()
      .range([chartHeight, 0]);

    const colorScale = d3.scaleOrdinal(d3[colorScheme]);

    const g = svg
      .append("g")
      .attr("transform", `translate(${margin.left},${margin.top})`);

    const bars = g
      .append("g")
      .selectAll("g")
      .data(stackData)
      .enter()
      .append("g")
      .attr("fill", (d, i) => {
        if (i > 0 && colorScale(i) === colorScale(i - 1)) {
          return colorScale(i + 1);
        }
        return colorScale(i);
      });

    bars
      .selectAll("rect")
      .data((d) => d)
      .enter()
      .append("rect")
      .attr("x", (d) => xScale(0))
      .attr("y", (d) => yScale(d[1]))
      .attr("height", (d) => yScale(d[0]) - yScale(d[1]))
      .attr("width", xScale.bandwidth())
      .attr("rx", 10) // Horizontal radius for rounded corners
      .attr("ry", 10) // Vertical radius for rounded corners
      .attr("stroke", "black");
    bars
      .selectAll("text")
      .data((d) => d)
      .enter()
      .append("text")
      .attr("x", (d) => xScale(0) + xScale.bandwidth() / 2)
      .attr("y", (d) => (yScale(d[0]) + yScale(d[1])) / 2)
      .attr("dy", "0.20em")
      .attr("text-anchor", "middle")
      .attr("fill", "white")
      .text((d, i, nodes) => {
        const recipe = filteredData.find(
          (r) => r.recipe_name === nodes[i].parentNode.__data__.key
        );
        if (recipe) {
          return `${recipe.recipe_name} (${recipe.total_run_time_per_day}m, ${(
            recipe.total_run_time_per_day / 60
          ).toFixed(1)}h, ${recipe.num_runs_per_day.toFixed(0)} runs)`;
        }
        return "";
      });

    // Draw the maxRecipesRunPerDay line
    g.append("line")
      .attr("x1", 0)
      .attr("x2", width)
      .attr("y1", yScale(maxRecipesRunPerDay))
      .attr("y2", yScale(maxRecipesRunPerDay))
      .attr("stroke", "red")
      .attr("stroke-width", 3)
      .attr("stroke-dasharray", "4");

    // Add the y-axis
    g.append("g").call(d3.axisLeft(yScale));

    // Add the x-axis
    g.append("g")
      .attr("transform", `translate(0, ${chartHeight})`)
      .call(d3.axisBottom(xScale))
      .selectAll("line")
      .style("stroke-width", 2); // Make the x-axis lines bolder

    // Add gridlines
    g.append("g")
      .attr("class", "grid")
      .call(
        d3
          .axisLeft(yScale)
          .tickSize(-width)
          .tickFormat("")
          .tickSizeOuter(0)
          .tickPadding(10)
      )
      .selectAll(".tick line")
      .attr("stroke", "lightgrey")
      .attr("stroke-opacity", 0.3);

    // Update y-ticks
    const yTicks = yScale.ticks().filter((d) => d !== 0);
    setYTicks(yTicks);

    // Render HTML table for recipes with run_duration < 2 min
    const shortRecipes = data.filter((d) => d.run_duration < 2);

    d3.select("#short-recipes-table").selectAll("*").remove();

    const table = d3
      .select("#short-recipes-table")
      .append("table")
      .style("border-collapse", "collapse")
      .style("width", "100%")
      .style("border", "1px solid black")
      .style("margin-top", "30px");

    table
      .append("caption")
      .style("caption-side", "top")
      .style("font-size", "24px")
      .style("margin-bottom", "10px")
      .text("Free Recipe Runs");

    table
      .append("thead")
      .append("tr")
      .selectAll("th")
      .data([
        "Recipe Name",
        "Run Duration",
        "Frequency of Run",
        "Total Duration per Day",
      ])
      .enter()
      .append("th")
      .style("border", "1px solid black")
      .style("text-align", "center")
      .style("padding", "5px")
      .text((d) => d);

    const tbody = table.append("tbody");

    shortRecipes.forEach((d, i) => {
      const row = tbody
        .append("tr")
        .style("background-color", i % 2 === 0 ? "#f2f2f2" : "white");

      row
        .append("td")
        .style("border", "1px solid black")
        .style("text-align", "center")
        .style("padding", "5px")
        .text(d.recipe_name);
      row
        .append("td")
        .style("border", "1px solid black")
        .style("text-align", "center")
        .style("padding", "5px")
        .text(d.run_duration);
      row
        .append("td")
        .style("border", "1px solid black")
        .style("text-align", "center")
        .style("padding", "5px")
        .text(d.frequency_of_run);
      row
        .append("td")
        .style("border", "1px solid black")
        .style("text-align", "center")
        .style("padding", "5px")
        .text(d.total_run_time_per_day.toFixed(1));
    });
  }, [data, maxRecipesRunPerDay, width, height, colorScheme]);

  const handleChangeColorScheme = (e) => {
    setColorScheme(e.target.value);
  };

  return (
    <div>
      <button
        onClick={downloadChart}
        className="bg-blue-100 text-black p-2 rounded"
        style={{ position: "absolute", top: "10px", right: "10px" }}
      >
        Download Chart
      </button>
      <div className="rounded-md border border-gray-300 bg-blue-200 p-4">

        <label>
          Width:
          <input
            type="number"
            value={width}
            onChange={handleWidthChange}
            className="ml-2 p-2 border rounded-md"
          />
        </label>
        <label className="ml-4">
          Height:
          <input
            type="number"
            value={height}
            onChange={handleHeightChange}
            className="ml-2 p-2 border rounded-md"
          />
        </label>
     
        <label htmlFor="color-scheme-select" className="ml-4">
          Color Scheme:
        </label>
        <select
          id="color-scheme-select"
          value={colorScheme}
          onChange={handleChangeColorScheme}
        >
          <option value="schemeCategory10">Category 10</option>
          <option value="schemeAccent">Accent</option>
          <option value="schemeDark2">Dark 2</option>
          <option value="schemePaired">Paired</option>
          <option value="schemePastel1">Pastel 1</option>
          <option value="schemePastel2">Pastel 2</option>
          <option value="schemeSet1">Set 1</option>
          <option value="schemeSet2">Set 2</option>
          <option value="schemeSet3">Set 3</option>
        </select>
      </div>
      <div ref={wrapperRef}>
        <svg ref={ref} width={width} height={height}></svg>
        <div id="short-recipes-table"></div>
      </div>
    </div>
  );
};

export default BarChart;
