import React from 'react';
import { Editor } from '@monaco-editor/react';

const CsvEditor = ({ content, onChange }) => {
  const handleAddSampleCsv = () => {
    const sampleCsv = `recipe_name,run_duration,frequency_of_run
get_oppty,10,every 30 min
get_acct,3,every 15 min
data_load,1,every 45 min
report_gen,5,every 60 min
sync_users,2,every 20 min
update_records,10,every 15 min`
    onChange(sampleCsv);
  };

  return (
    <div>
      <button onClick={handleAddSampleCsv} id="add-sample-csv"  className="bg-blue-500 text-white py-2 px-4 rounded-md mb-4">
        Add Sample CSV
      </button>
      <Editor
        height="50vh"
        width={"600px"}
        defaultLanguage="csv"
        value={content}
        onChange={onChange}
        theme="vs-dark"
      />
    </div>
  );
};

export default CsvEditor;
